@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
*, *::before, *::after {
  box-sizing: border-box;
}
html,body{
  padding: 0;
  margin: 0;
  /* background: #000; */
  /* color: #fff; */
}

#root {
  width: 80%;
  margin: 0 auto;
}

.sort-btn  {
  background: #3085e8;
  padding: 6px 16px;
  border-radius: 2px;
  color: #fff;
  font-size: 14px;
}

.options {
  margin-top: 100px;
}

.options-item {
  margin: 0 40px 15px 0;
}

.sort-nums {
  max-height: 100px;
  overflow: scroll;
}

.cards {
  position: relative;
  height: 400px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  /* background-color: #000; */
}

.card-wrapper {
  position: absolute;
  min-width: 1px;
  bottom: 0;
  border: 1px solid transparent;
  transition: -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -webkit-transition: -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.card-wrapper.card-no-border{
  border: none;
}

.card {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: #ff3179;
}

.card-active {
  -webkit-filter: hue-rotate(200deg);
          filter: hue-rotate(200deg);
}

.card-locked {
  -webkit-filter: hue-rotate(280deg);
          filter: hue-rotate(280deg);
}

@media only screen and (max-width: 420px) {
  .options {
    margin-top: 20px;
  }
  .cards{
    height: 350px;
  }
  .options-item {
    display: block;
  }
}
.time-wrap {
  padding: 20px;
}
.time-detail {
  margin-top: 30px;
}
